.profile-container {
    // align-items: center;
    // justify-content: center;
    padding-top: 20px;
    
    
    
    background: linear-gradient(
        0deg,
        rgba(255, 255, 255, 0) 0%,
        rgba(255, 255, 255, 0.8) 10%,
        rgba(255, 255, 255, 1) 40%,
        rgba(255, 255, 255, 1) 100%
        );

    .profile-nav{
        width: 100%;
        margin-bottom: 1rem;
        
        &>*:not(:last-child){
            margin-inline-end: 15px;
        }
        button {
            border: 0;
            padding: 0;
            cursor: pointer;
            
        }
        button::after {
            display: none;
        }
       
        
    }

    

    .profile-content {
        display: flex;
        flex-direction: row;
        padding: 0;
        gap: 50px;
        padding: 0;
        margin-top: 40px;
        display: flex;
        justify-content: flex-start;

    align-items: flex-start;
    grid-column: 2/span 1;

        .user-card {
            width: 30%;
            display: flex;
            flex-direction: column;
            max-height: 300px;
            border: 1px solid #dddddd;
            border-radius: 10px;
            align-items: center;
            padding: 20px 40px;
            margin-top: 28px;
    
            .user-avatar {
                height: 100px;
                width: 100px;
                border-radius: 50%;
                object-fit: cover;
                margin-bottom: 10px;
            }

            h5 {
                font-family: circular-medium;
            }
        }
        .user-stats {
        
            .user-stays {
                width: 30%;
                align-self: baseline;
            }
        }
    } 
}

.stays-info {
    display: flex;
    flex-direction: column;
    .host-chart {

        canvas {
            height: 250px !important;
            // width: 500px !important;
            margin-bottom: 40px;
            justify-self: center!important;
        }
    }
}


