    color: #FFFFFF ;
    cursor: pointer ;
    display: inline-block ;
    font-family: inherit ;
    font-size: 16px ;
    font-weight: 600 ;
    line-height: 16px ;
    margin: 0px ;
    outline: none ;
    overflow: hidden ;
    padding: 0px ;
    text-align: inherit ;
    text-decoration: none ;
    -webkit-user-select: auto ;
    -moz-user-select: auto ;
    -ms-user-select: auto ;
    user-select: auto ;
    background-color: #FF385C ;
    border-radius: 24px ;
    height: 48px ;
    max-width: 48px ;
    position: relative ;
    -webkit-transition: 0.2s max-width cubic-bezier(0.35, 0, 0.65, 1) ;
    -moz-transition: 0.2s max-width cubic-bezier(0.35, 0, 0.65, 1) ;
    transition: 0.2s max-width cubic-bezier(0.35, 0, 0.65, 1) ;
    vertical-align: middle ;
    z-index: 0 ;
}
._sxfp92z {
    background-color: var(--header-brand-color, #FF385C) ;
}
._sxfp92z::before {
    background: -webkit-linear-gradient(to right, #E61E4D 0%, #E31C5F 50%, #D70466 100%) ;
    background: -moz-linear-gradient(to right, #E61E4D 0%, #E31C5F 50%, #D70466 100%) ;
    background: linear-gradient(to right, #E61E4D 0%, #E31C5F 50%, #D70466 100%) ;
    bottom: 0px ;
    content: "" ;
    left: 0px ;
    position: absolute ;
    right: 0px ;
    top: 0px ;
    -webkit-transition: 0.2s opacity cubic-bezier(0.35, 0, 0.65, 1) ;
    -moz-transition: 0.2s opacity cubic-bezier(0.35, 0, 0.65, 1) ;
    transition: 0.2s opacity cubic-bezier(0.35, 0, 0.65, 1) ;
    will-change: opacity ;
    z-index: 0 ;
    opacity: 0 ;
}

